.nav {
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex; /* щоб зробити відстані між списком ставимо все в ряд */
    flex-direction: column; /* а потім ставимо все у стовпчик */
    gap: 32px;
  }

  &__link {
    position: relative;
    color: $menu-text-color;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    &::after {
      content: '';
      position: absolute;
      bottom: -8px;
      display: block;
      height: 1px;
      width: 100%;
      background-color: $menu-text-color;

      transition: transform $effect-duration;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }
}
